import React, { Component, PropTypes } from 'react';


class FixBackgroundPhotoText extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isInViewport: false,
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.position);
    document.addEventListener('scroll', this.position);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.position);
    document.removeEventListener('scroll', this.position);
  }

  position = () => {
    const { isInViewport } = this.state;
    const viewportHeight = window.innerHeight;
    const self = this.self;
    const selfDimensions = self.getBoundingClientRect();
    const { top, bottom } = selfDimensions;
    const nextIsInViewport = (bottom > 0 && top < viewportHeight);
    if (isInViewport === nextIsInViewport) {
      return;
    }
    this.setState({
      isInViewport: nextIsInViewport,
    });
  }
  render() {
//    const { isInViewport } = this.state;
//    const viewportHeight = window.innerHeight;
//    const selfDimensions = self.getBoundingClientRect();
//    const { top, bottom } = selfDimensions;
//    const nextIsInViewport = (bottom > 0 && top < viewportHeight);
//    if (isInViewport === nextIsInViewport) {
//      return;
//    }
//    this.setState({
//      isInViewport: nextIsInViewport,
//    });
    const { isInViewport } = this.state;
    const { src } = this.props;
    const containerStyle = {
      overflow: 'hidden',
    };
    const backgroundPhotoStyle = {
      top: '50%',
      left: '50%',
      position: 'fixed',
      minWidth: '100%',
      minHeight: '100%',
      width: 'auto',
      height: 'auto',
      transform: 'translateX(-50%) translateY(-50%)',
      zIndex: '-100000',
      backgroundSize: 'cover',
      display: isInViewport ? '' : 'none',
    };
    return (
      <div ref={(c) => { this.self = c; }} style={containerStyle}>
        <img
          style={backgroundPhotoStyle}
          src={src}
        >
          
        </img>
        {this.props.children}
      </div>
    );
  }
}

FixBackgroundPhotoText.propTypes = {
  imgSrc: PropTypes.string.isRequired,
};

export default FixBackgroundPhotoText;



